import React, { useEffect, useState } from "react";
import { getCGTokens } from "../../helpers";
import orderBy from "lodash/orderBy";
import { ListItem, TextPercentage, TokenIcon } from "./TokenTable.style";
import { Spin, List } from "antd";
import { ListItemPart } from "./TokenTable.style";
// import { TableContainer } from "../../App.style";

// var formatter = new Intl.NumberFormat("en-US", {
//   style: "currency",
//   currency: "PHP",
// });

export const TokenTable = () => {
  const [state, setState] = useState({ tokens: [], loading: true });
  const { tokens, loading } = state;
  useEffect(() => {
    initTable();
  }, []);
  const initTable = async () => {
    try {
      const result = await getCGTokens();
      setState((prev) => ({ ...prev, loading: false, tokens: result }));
    } catch (error) {
      setState((prev) => ({ ...prev, loading: false, tokens: [] }));
    }
  };
  return (
    <Spin spinning={loading} tip="Loading...">
      <List
        size="small"
        header={
          <ListItem>
            <ListItemPart>Cryptocurrency</ListItemPart>
            <ListItemPart>Today</ListItemPart>
            <ListItemPart align="right" width="100px">
              24Hrs
            </ListItemPart>
          </ListItem>
        }
        footer={<div>&nbsp;</div>}
        bordered
        dataSource={orderBy(tokens, ["php_24h_change"], ["desc"])}
        renderItem={(token) => (
          <ListItem>
            {/* <ListItemPart width="60px" align="center">
              <img width={14} src={token.icon} />
            </ListItemPart> */}
            <ListItemPart>
              <TokenIcon width={14} src={token.icon} /> {token.name}
            </ListItemPart>
            <ListItemPart>
              P{token.php}{" "}
              <span className="text-secondary">(${token.usd})</span>
            </ListItemPart>
            <ListItemPart align="center" width="150px">
              <TextPercentage positive={token.php_24h_change > 0}>
                {token.php_24h_change.toFixed(2)}%
              </TextPercentage>
            </ListItemPart>
          </ListItem>
        )}
      />
    </Spin>
  );
};
